// rce is been used for react class based component
// rcep is been used for react class based component along with the poptypes

// import React, { Component } from "react";
import React from 'react'
import{Link}from "react-router-dom";

const NavBar = () => {
  // render() { //used i the class based componets not in the function based components
    return (
      <div>
        <nav className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark">
          <div className="container-fluid">
            <Link className="navbar-brand" to="/">
              NewsHub
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item">
                  <Link className="nav-link active" aria-current="page" to="/">
                    Home
                  </Link>
                </li>
                
                <li className="nav-item">
                  <Link className="nav-link" to="bussiness">Bussiness</Link></li>
                <li className="nav-item">
                  <Link className="nav-link" to="entertainment">Entertainment</Link></li>
                <li className="nav-item">
                  <Link className="nav-link" to="general">General</Link></li>
                <li className="nav-item">
                  <Link className="nav-link" to="health">Health</Link></li>
                <li className="nav-item">
                  <Link className="nav-link" to="science">Science</Link></li>
                <li className="nav-item">
                  <Link className="nav-link" to="sports">Sports</Link></li>
                <li className="nav-item">
                  <Link className="nav-link" to="technology">Technology</Link></li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    );
  }
// }

export default NavBar;
